import React from "react";
import {Link} from "react-router-dom";
import {useHistory} from "react-router-dom/cjs/react-router-dom.min";
import "./Stylies/Nav.css";
import Logo from "./Images/Logo.png";
import menu from "./Images/Menu.svg";
import {useState} from "react/cjs/react.development";
import {auth} from "./firebaseConfig";
import {useAuthState} from "react-firebase-hooks/auth";
export default function Nav() {
  const [state, setState] = useState();
  const history = useHistory();
  const [user] = useAuthState(auth());

  return (
    <header className='header--nav'>
      <nav className='navigation--nav'>
        <img
          src={Logo}
          className='logo--nav'
          alt=''
          onClick={() => history.push("/")}
        />
        <ul
          className={
            state === true ? "list-ul--nav on--nav" : "list-ul--nav off--nav"
          }
        >
          <Link
            to='/'
            style={{textDecoration: "none"}}
            onClick={() => setState(!state)}
          >
            <li className='list--nav'>Home</li>
          </Link>

          <Link
            to={!user ? "/Contact" : "/join"}
            style={{textDecoration: "none"}}
            onClick={() => setState(!state)}
          >
            <li className='list--nav'>{!user ? "Contact" : "Rooms"}</li>
          </Link>
          <Link
            to='/About'
            style={{textDecoration: "none"}}
            onClick={() => setState(!state)}
          >
            <li className='list--nav'>About</li>
          </Link>
          <Link
            to='Join'
            style={{textDecoration: "none"}}
            onClick={() => {
              setState(!state);
              if (user) {
                history.push("/");
                auth().signOut();
              }
            }}
          >
            <li className='cta--nav'>{user ? "Sign Out" : "Join"}</li>
          </Link>
        </ul>
        <img
          src={menu}
          onClick={() => setState(!state)}
          alt='test'
          className='menu--nav'
        />
      </nav>
    </header>
  );
}
