import React from "react";
import "../Stylies/About.css";
import {useHistory} from "react-router";
export default function About() {
  const history = useHistory();
  return (
    <div className='main--about'>
      <h1 className='header--about'>About me</h1>
      <p className='p--about'>
        Hello, my name is Nelu and I'm a developer from Moldova.{" "}
      </p>
      <p className='p--about'>
        I decided to create this website to test my coding skills and provide
        the world a new way of catting.
      </p>
      <p className='p--about'>
        On this website, you can talk or debate with other people on different
        topics such as sports, politics, life, and others. All of this is being
        held in rooms. You can join any room or create your own with your topic.
      </p>
      <p className='p--about'>
        This project isn't intended for generating revenue. It doesn't run any
        sort of advertisements on it.
      </p>
      <p className='p--about'>Hop on and let's start chatting!</p>
      <button onClick={() => history.push("/")} className='cta--about'>
        Hop on
      </button>
    </div>
  );
}
