import React from "react";
import {useState} from "react/cjs/react.development";
import {db} from "./firebaseConfig";
import {useHistory} from "react-router-dom/cjs/react-router-dom.min";

import "./Stylies/RoomCreate.css";
function RoomCreate() {
  const [name, setName] = useState("");
  const [topic, setTopic] = useState("");
  const history = useHistory();

  async function createRoom(e) {
    e.preventDefault();

    await db().collection("Rooms").doc(name).set({Name: name, Topic: topic});
    history.push(`/page/${name}`);
  }

  return (
    <form onSubmit={createRoom} className='form-main--RoomCreate'>
      <div className='inputs-row--RoomCreate'>
        <div className='inputs-blurb--RoomCreate'>
          <h1>
            Enter room <span style={{color: "#FFCC29"}}>name</span>
          </h1>
          <input
            type='text'
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder='Name'
            onfocus=''
          />
        </div>
        <div className='inputs-blurb--RoomCreate'>
          <h1>
            Enter room <span style={{color: "#FFCC29"}}>topic</span>
          </h1>
          <input
            type='text'
            value={topic}
            onChange={(e) => setTopic(e.target.value)}
            placeholder='Topic'
          />
        </div>
      </div>

      <button type='submit' className='submit-button--RoomCreate'>
        Create room
      </button>
    </form>
  );
}

export default RoomCreate;
