import {
  Route,
  BrowserRouter as Router,
  Switch,
} from "react-router-dom/cjs/react-router-dom.min";

import SignIn from "./components/SignIn";

import Landing from "./components/MainPages/Landing";
import About from "./components/MainPages/About";
import Contact from "./components/MainPages/Contact";
import Nav from "./components/Nav";
import Messages from "./components/Messages";
import RoomCreate from "./components/RoomCreate";
import "./components/Stylies/Main.css";

function App() {
  return (
    <Router>
      <div className='container'>
        <Nav />
        <Switch>
          <Route path='/' exact component={Landing} />
          <Route path='/Join' component={SignIn} />
          <Route path='/About' component={About} />
          <Route path='/Contact' component={Contact} />
          <Route path='/Create room' component={RoomCreate} />
          <Route path='/page/:RMID'>
            <Messages roomName />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
