import React from "react";
import "../Stylies/Contact.css";
import mailIcon from "../Images/mail.svg";
import emailjs from "emailjs-com";
import {init} from "emailjs-com";
import {useRef} from "react";
init("user_0viCUg6SixpFKXaCQP7ph");

export default function Contact() {
  const form = useRef();
  const sendInformation = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_nmqu4ue",
        "template_tt15tt1",
        form.current,
        "user_0viCUg6SixpFKXaCQP7ph"
      )
      .then(
        (result) => {
          alert("Message sent successfully");
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
  };

  return (
    <div className='page--contact-contact'>
      <div className='contact--info-contact'>
        <h1 className='heading--text-contact'>Contact us!</h1>
        <div className='mailinfo--text-contact'>
          <img src={mailIcon} alt='' className='icon--mail-contact' />
          <p className='mail--text-contact'>casualtalks70@gmail.com</p>
        </div>
      </div>
      <form
        ref={form}
        className='message--form-contact'
        onSubmit={sendInformation}
      >
        <div className='input--text-contact'>
          <p>Your name</p>
          <input type='name' placeholder='Name' name='name' />
        </div>
        <div className='input--text-contact'>
          <p>Your email</p>
          <input type='email' placeholder='Email' name='email' />
        </div>
        <div className='input--text-contact input--text-msg-contact'>
          <p>Your message</p>
          <textarea type='text' placeholder='Message' name='message'></textarea>
        </div>
        <button type='submit' className='button--send-contact'>
          Send!
        </button>
      </form>
    </div>
  );
}
