import React, {useState, useEffect} from "react";
import {db} from "./firebaseConfig";
import {useHistory} from "react-router-dom/cjs/react-router-dom.min";
import "./Stylies/Rooms.css";

export default function Rooms() {
  const [rooms, setRooms] = useState([]);
  const history = useHistory();

  function selectedRoom(RMID) {
    history.push(`/page/${RMID}`);
  }
  useEffect(() => {
    db()
      .collection("Rooms")
      .onSnapshot((snap) => {
        setRooms(snap.docs.map((doc) => doc.data()));
      });
  }, []);
  return (
    <div className='main--Rooms'>
      <div className='room-section--Rooms'>
        {rooms.map(({id, Name, Topic}) => (
          <div key={id} className='room-rows--Rooms'>
            <div className='room-stats--Rooms'>
              <h1 className='text-length--Rooms'>{Name}</h1>
              <h2 className='text-length--Rooms'>
                <span style={{color: "#F58634"}} className='text-length--Rooms'>
                  Topic:{" "}
                </span>
                {Topic}
              </h2>
              <div className='line--Rooms'>
                <hr />
              </div>
            </div>

            <button
              onClick={() => selectedRoom(Name)}
              className='join-button--Rooms'
            >
              Enter
            </button>
          </div>
        ))}
      </div>

      <div>
        <button
          onClick={() => history.push("/Create room")}
          className='create-room--Rooms'
        >
          Create room
        </button>
      </div>
    </div>
  );
}
